.wp-block-zolo-team-child {
    // variable declaration
    --zolo-img-margin: 0 0 20px;
    --zolo-item-padding: clamp(1.563rem, 4vw, 2.5rem);
    --zolo-dasignation-margin: 5px 0 0 0;
    --zolo-desc-margin: 10px 0 0 0;
    --zolo-name-text-color: #2e2e35;
    --zolo-desc-text-color: #6e6e7b;
    --zolo-dasignation-text-color: #4b445a;
    --zolo-external-btn-padding: 14px;
    --zolo-external-icon-size: 16px;
    --zolo-social-icon-size: 14px;
    --zolo-social-item-gap: 10px;
    --zolo-social-btn-padding: 14px;
    --zolo-social-border-color: #ddd;
    --zolo-social-border-width: 1px;
    --zolo-social-border-style: solid;
    --zolo-social-and-link-wrap-padding: clamp(1.25rem, 3vw, 1.875rem) 0 0 0;
    --zolo-social-and-link-wrap-margin: clamp(1.875rem, 5vw, 3.125rem) 0 0 0;
    --zolo-social-and-link-wrap-border-width: 1px;
    --zolo-social-and-link-wrap-border-style: solid;
    --zolo-social-and-link-wrap-border-color: #ddd;
    --zolo-social-and-link-wrap-gap: 10px;
    --zolo-content-margin: clamp(-1.25rem, 3vw, -1.875rem) 0 0 clamp(1.563rem, 3.5vw, 2.188rem);
    --zolo-content-padding: clamp(1.563rem, 4vw, 2.5rem);
    --zolo-content-border-color: #f0f0f0;
    --zolo-content-border-width: 1px;
    --zolo-content-border-style: solid;
    --zolo-social-wrap-margin: 30px;

    box-sizing: border-box;
    min-width: 100%;
    .zolo-item {
        overflow: hidden;
        position: relative;
    }
    .zolo-image-wrap {
        position: relative;
    }

    img {
        max-width: 100%;
        width: 100%;
        height: auto;
        vertical-align: middle;
        box-sizing: border-box;
        border-radius: var(--zolo-item-border-radius);
    }

    // name
    .zolo-name {
        font-size: clamp(1.25rem, 2.5vw, 1.5rem);
        text-transform: capitalize;
        text-decoration: none;
        font-weight: 600;
        color: var(--zolo-name-text-color);
        display: block;
        transition: color 0.3s ease;
    }

    // designation
    .zolo-designation {
        font-size: clamp(0.75rem, 1.5vw, 0.875rem);
        font-weight: 500;
        color: var(--zolo-dasignation-text-color);
        margin: var(--zolo-dasignation-margin);
    }

    // description - short bio
    .zolo-desc {
        font-size: clamp(0.75rem, 1.5vw, 0.875rem);
        color: var(--zolo-desc-text-color);
        margin: var(--zolo-desc-margin);
        font-weight: 400;
    }
    // social share
    .zolo-social-share {
        display: inline-flex;
        gap: var(--zolo-social-item-gap);
        align-items: center;
        width: 100%;
        box-sizing: border-box;
    }
    .zolo-social-link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        box-sizing: content-box;
        transition: 0.3s ease-out;
        border-radius: 10px;
        &:hover {
            background-color: #000;
            svg {
                fill: var(--zoloblocks-brand-button-color);
            }
        }
        svg {
            height: var(--zolo-social-icon-size);
            width: var(--zolo-social-icon-size);
            z-index: 1;
            position: relative;
            display: block;
        }
    }

    .zolo-social-link,
    .zolo-external-link {
        position: relative;
        overflow: hidden;
    }

    .zolo-social-link {
        padding: var(--zolo-social-btn-padding);
        border: var(--zolo-social-border-width) var(--zolo-social-border-style) var(--zolo-social-border-color);
    }
    .zolo-external-link {
        padding: var(--zolo-external-btn-padding);
    }

    // detail profile link
    .zolo-external-link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background-size: 200% auto;
        background-color: var(--zoloblocks-brand-color);
        transition: 500ms ease;
        text-decoration: none;
        border-radius: 50%;
        svg {
            height: var(--zolo-external-icon-size);
            width: var(--zolo-external-icon-size);
            display: block;
            fill: var(--zoloblocks-brand-button-color);
            z-index: 1;
            position: relative;
        }
        &:hover {
            background-color: var(--zoloblocks-brand-color-hover);
        }
    }

    &.default {
        .zolo-info-wrap {
            --zolo-content-padding: 20px 0 0;
            // display: flex;
            // align-items: flex-end;
            gap: 20px;
            padding: var(--zolo-content-padding);
        }
        .zolo-social-share {
            position: absolute;
            top: 0;
            left: 0;
        }

        .zolo-link-btn {
            position: absolute;
            bottom: 0;
            right: 0;
            transition: transform 0.3s ease;
            a {
                margin: 30px;
            }
        }
        .zolo-item {
            .zolo-social-link {
                transform: translateX(-20px);
            }
            &:hover {
                .zolo-link-btn {
                    transform: scale(1.2);
                }
            }
        }
    }

    // &.default,
    &.style-1 {
        .zolo-social-and-link-wrap {
            border-top: var(--zolo-social-and-link-wrap-border-width) var(--zolo-social-and-link-wrap-border-style)
                var(--zolo-social-and-link-wrap-border-color);
            padding: var(--zolo-social-and-link-wrap-padding);
            margin: var(--zolo-social-and-link-wrap-margin);
        }
    }

    &.style-1 {
        .zolo-item {
            background-color: var(--zoloblocks-item-bg-color);
            border-width: var(--zolo-item-border-width);
            border-style: var(--zolo-item-border-style);
            border-color: var(--zoloblocks-item-border-color);
            padding: var(--zolo-item-padding);
            border-radius: var(--zolo-item-border-radius);
            text-align: center;
        }
        .zolo-info-wrap {
            z-index: 99;
            position: relative;
        }
        .zolo-image-wrap {
            margin: var(--zolo-img-margin);
        }
        .zolo-social-and-link-wrap {
            display: flex;
            align-items: center;
            gap: var(--zolo-social-and-link-wrap-gap);
            justify-content: space-between;
        }

        .zolo-external-link {
            border-radius: 50%;
            font-size: clamp(0.75rem, 1vw, 0.875rem);
        }
    }

    &.style-2 {
        .zolo-item {
            position: relative;
            &:hover {
                .zolo-social-link {
                    transform: translateX(0);
                    opacity: 1;
                }
                .zolo-image-wrap {
                    img {
                        transform: scale(1.1);
                    }
                    &:before {
                        height: 100%;
                    }
                }
                .zolo-link-btn {
                    transform: translateY(0);
                    opacity: 1;
                }
            }
        }

        .zolo-name {
            --zolo-name-text-color: #fff;
            color: var(--zolo-name-text-color);
        }

        .zolo-designation {
            --zolo-dasignation-text-color: #eaeaea;
            color: var(--zolo-dasignation-text-color);
        }

        .zolo-desc {
            --zolo-desc-text-color: #e1e1e1;
            color: var(--zolo-desc-text-color);
        }

        .zolo-link-btn {
            transform: translateY(20px);
            opacity: 0;
            transition:
                transform 0.3s ease,
                opacity 0.3s ease;
        }

        .zolo-image-wrap {
            display: flex;
            flex-direction: row-reverse;
            border-radius: var(--zolo-item-border-radius);
            overflow: hidden;
            img {
                transition: transform 0.3s ease;
            }
            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 35%;
                background: linear-gradient(0deg, rgba(1, 17, 40, 0.399), transparent);
                z-index: 1;
                transition: height 0.3s ease;
            }
        }
        .zolo-social-share {
            position: absolute;
            top: 0;
            right: 0;
        }
        .zolo-social-link {
            transform: translateX(20px);
        }

        .zolo-info-wrap {
            --zolo-content-padding: 30px;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            gap: clamp(0.938rem, 2vw, 1.25rem);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 100%;
            padding: var(--zolo-content-padding);
            box-sizing: border-box;
            z-index: 1;
        }
        .zolo-external-link {
            --zolo-external-btn-padding: 16px;
            padding: var(--zolo-external-btn-padding);
            // border-radius: 50%;
        }
    }

    &:not(.style-1) {
        .zolo-social-share {
            margin: var(--zolo-social-wrap-margin);
        }
    }

    &.style-2,
    &.default {
        .zolo-item {
            position: relative;
            &:hover {
                .zolo-social-link {
                    transform: translateX(0);
                    opacity: 1;
                }
            }
        }

        .zolo-social-share {
            width: auto;
            flex-direction: column;
        }

        .zolo-social-link {
            opacity: 0;
            transition:
                transform 0.3s ease,
                opacity 0.3s ease;
            background-color: transparent;
            border: 1px solid rgba(44, 44, 48, 0.47);
            svg {
                fill: #000;
            }
            &:hover {
                svg {
                    fill: #fff;
                }
                border-color: #000;
                background-color: #000;
            }
            &:nth-child(1) {
                transition-delay: 0.1s;
            }
            &:nth-child(2) {
                transition-delay: 0.2s;
            }
            &:nth-child(3) {
                transition-delay: 0.3s;
            }
            &:nth-child(4) {
                transition-delay: 0.4s;
            }
            &:nth-child(5) {
                transition-delay: 0.5s;
            }
            &:nth-child(6) {
                transition-delay: 0.6s;
            }
            &:nth-child(7) {
                transition-delay: 0.7s;
            }
        }
    }
}
